


















export default {
	data() {
		return {
			mobile: '',
			verifyCode: '',
			timer: 60,
			disabled: false,
			title: '发送验证码',
			nextdisabled: false
		};
	},
	onLoad() {},
	methods: {
		getVerifyCode: function() {
			var _this = this;
			if (_this.mobile.length < 11) {
				uni.showToast({
					title: '手机号不符合要求',
					duration: 2000,
					icon: 'none'
				});
				return;
			} else {
				_this.disabled = true;
				let timer1 = setInterval(() => {
					_this.timer--;
					_this.title = _this.timer + '秒';
					if (_this.timer == 0) {
						clearInterval(timer1);
						_this.timer = 60;
						_this.disabled = false;

						// _this.nextdisabled = false;

						_this.title = '发送验证码';
						return;
					}
				}, 1000);
				uni.request({
					url: _this.apiServer + '/user/verify',
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						mobile: _this.mobile
					},
					success: res => {
						if (res.data.code === 0) {
							uni.showToast({
								title: '验证码已发送'
							});
							_this.disabled = true;
							console.log(_this.disabled);
						} else {
							uni.showModal({
								title: '提示',
								content: res.data.msg
							});
						}
					}
				});
			}
		},
		checkCode: function() {
			var _this = this;
			console.log(_this.verifyCode);
			console.log(_this.mobile);
			uni.request({
				url: this.apiServer + '/user/check',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					mobile: _this.mobile,
					verifyCode: _this.verifyCode
				},
				success: res => {
					console.log(res.data.code);
					if (res.data.code === 0) {
						uni.navigateTo({
							url: '../register/password?mobile=' + _this.mobile
						});
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
				}
			});
		}
	}
};
